
body {
	overflow-x: hidden;
	margin: 0;
}

.heroBackground{
	margin: 0;
	width: 100%;
  	height: 70rem;
	background-color: #ABABAB;	
	overflow-x: hidden;
}

/////////////////////
// BATS ANIMATION //
//////////////////
.bats svg {
	position: absolute;
	margin-top: 12rem;
    transform: scale(0);
	animation-name: slideOutRight-Slower;
	animation-duration: 8s;
	animation-delay: 2s;
}

@media (min-width: 700px) {
	.bats svg {
		margin-top: 4rem;
	    animation-name: slideOutRight;
	    animation-duration: 8s;
	    animation-delay: 2s;
	    
	}
}
// ANIMATION /////////////////////////////
// slides the bats to the right off screen
@keyframes slideOutRight {
  from {
  	transform: scale(100%);
    transform: translate3d(100%, 0, 0);
  }

  to {
    visibility: hidden;
    transform: scale(100%);
	transform: translate3d(1000%, 0, 0);
  }
}

// OLD ANIMATION THAT ONLY WORKED PROPERLY ON FIREFOX
