@keyframes slideOutRight-Slower {
  from {
  	transform: scale(100%);
    transform: translate3d(0, 0, 0);
  }

  to {
    visibility: hidden;
    transform: scale(100%);
	transform: translate3d(500%, 0, 0);
  }
}

///////////////////////////////////

///////////////////////////////////
//INFO ON HISTORY DAY CONTAINER //
//////////////////////////////////

.hdayContainer {
	height: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;	
	.widget{
		-ms-flex-item-align: end;
		    align-self: flex-end;	
		width: 100%;
	}
}

.hdayBox {
	margin: 0rem;

	.titleBox {
	 	background: rgba(0, 0, 0, 0.7);
		width: 100%;
		margin: 0;
		h2 {
			font-family: 'Lovers Quarrel', cursive;
			color: #FFEA6F;
			padding: 1rem 2rem 1rem 2rem;
			text-align: center;
			margin: 0;
			text-shadow: -3px 1px 3px rgba(0, 0, 0, 1);
		}
 	}
 	.borderBox {
 		background-color: rgba(0, 0, 0, 0.8);
 		border-radius: none;
 	}
 	.infoBox {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-direction: column;
		        flex-direction: column;
		-webkit-box-pack: center;
		    -ms-flex-pack: center;
		        justify-content: center;
		-webkit-box-align: center;
		    -ms-flex-align: center;
		        align-items: center;
		h3 {
			font-family: 'IM Fell English', serif;
			color: white;
			font-size: 2rem;
			padding: 1rem 2rem 1rem 2rem;
			margin: 2rem 0 0.75rem 0;
			text-align: center;
			border-bottom: 1px solid white;
		}
		img.ornament {
			display: none;
		}
		h4 {
			font-family: 'IM Fell English', serif;
			color: white;
			padding: 1rem 0rem 1rem 0rem;
			margin: 0;
			text-align: center;
			line-height: 1.5;
		}
		button {
			margin-top: 1rem;
			background-color: #515151;
			border: 1px solid white;
			margin-bottom: 2rem;
			cursor: pointer;
			&:hover {
				background-color: #8f2c2c;
				-webkit-transition: 1s ease-in-out;
				transition: 1s ease-in-out;
			}
			h4 {
				padding: 1rem;
				font-family: 'IM Fell English', serif;
				font-size: 1rem;
				color: white;
			}
		}
	}

 }

///////////////////
//MEDIA QUERIES //
/////////////////

@media (min-width: 700px) {
	
	// HISTORY DAY INFO CONTAINER
 	.hdayContainer {
 		.widget{
	 		width: auto;
	 		-ms-flex-item-align: center;
	 		    align-self: center;
			border-radius: 16px;
		}
	}
 	.titleBox {
 		background: none !important;
 		border-top-right-radius: 16px;
		border-top-left-radius: 16px;
		h2{
			font-size: 4rem;
		}
	}
	.borderBox {
		padding:  2rem;
		border-radius: 12px;
		.borderBackground {
	 		background: url("http://staging.traviscountyhistory.org/wp-content/uploads/2017/06/o-Border.png");
			background-repeat: no-repeat;
			background-size: contain;
			background-position: 50% 50%;
			.infoBox {
				padding: 2rem;
				h3 {
					border-bottom: none;
				}
				img.ornament {
					display: block !important;
					width: 40%;
					height: 40%;
				}
				button h4 {
					font-size: 2rem;
				}
			}
	 	}

	}
}

